export class TaxPolicyViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#tax_policy_view_container',

			status_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'TaxPolicyEditView.html';
		this.permission_id = 'tax_policy';
		this.viewId = 'TaxPolicy';
		this.script_name = 'TaxPolicyView';
		this.table_name_key = 'tax_policy';
		this.context_menu_name = $.i18n._( 'Tax' );
		this.navigation_label = $.i18n._( 'Tax' );
		this.api = TTAPI.APITaxPolicy;

		this.render();
		this.buildContextMenu();

		this.initData();
	}

	initOptions() {
		var $this = this;
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_tax': { 'label': $.i18n._( 'Tax' ) },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APITaxPolicy,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_tax_policy',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_tax = this.edit_view_tab.find( '#tab_tax' );

		var tab_tax_column1 = tab_tax.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_tax_column1 );

		var form_item_input;
		var widgetContainer;
		var label;

		//Product
		var args = { filter_data: {} };
		args.filter_data.type_id = 50;

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIProduct,
			layout_name: 'global_product',
			show_search_inputs: true,
			set_empty: true,
			field: 'product_id'
		} );
		form_item_input.setDefaultArgs( args );
		this.addEditFieldToColumn( $.i18n._( 'Product' ), form_item_input, tab_tax_column1, '' );

		//Code
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'code' } );
		this.addEditFieldToColumn( $.i18n._( 'Code' ), form_item_input, tab_tax_column1 );

		// Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_tax_column1 );

		form_item_input.parent().width( '45%' );

		// Tax Number

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'number' } );
		this.addEditFieldToColumn( $.i18n._( 'Tax Number' ), form_item_input, tab_tax_column1 );

		// Percent
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'percent', width: 80 } );

		widgetContainer = $( '<div class=\'widget-h-box\'></div>' );
		label = $( '<span class=\'widget-right-label\'>%</span>' );

		widgetContainer.append( form_item_input );
		widgetContainer.append( label );
		this.addEditFieldToColumn( $.i18n._( 'Percent' ), form_item_input, tab_tax_column1, '', widgetContainer, true );

		//Priority

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'priority', width: 40 } );
		this.addEditFieldToColumn( $.i18n._( 'Priority' ), form_item_input, tab_tax_column1 );

		//Display Balance on Pay Stub
		form_item_input = Global.loadWidgetByName( FormItemType.CHECKBOX );
		form_item_input.TCheckbox( { field: 'display_number' } );
		this.addEditFieldToColumn( $.i18n._( 'Display Number on Invoice' ), form_item_input, tab_tax_column1 );

		//Include Area Policy

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIAreaPolicy,
			allow_multiple_selection: true,
			layout_name: 'global_area_policy',
			show_search_inputs: true,
			set_empty: true,
			field: 'include_area_policy'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Include Area Policy' ), form_item_input, tab_tax_column1, '' );

		//Exclude Area Policy

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIAreaPolicy,
			allow_multiple_selection: true,
			layout_name: 'global_area_policy',
			show_search_inputs: true,
			set_empty: true,
			field: 'exclude_area_policy'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Exclude Area Policy' ), form_item_input, tab_tax_column1, '' );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 1,
				field: 'name',
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

}
